<script>
import { mapActions, mapGetters } from 'vuex';
import { Carousel, Slide } from 'vue-carousel';
import { commonMixin } from '../../mixins/common';

import { queryProgressNewConTract } from '../../service';
import { queryProgressLoan } from '../../service';
import { queryPayRecord } from '../../service'
import { queryPremiumRecord } from '../../service'
import { queryInsuranceClaimRecord } from '../../service'
import { queryIntegratedMarketing } from '../../service'

import MarketingDataModal from '../components/MarketingDataModal';
import Avatar from '../../components/Avatar';
import CustomerInfo from '../components/CustomerDetail/CustomerInfo';
import TabHeader from '../components/CustomerDetail/TabHeader';

export default {
  name: 'CustomerDetail6',
  mixins: [commonMixin],
  data () {
    return {
      isLoading_1: false,
      isLoading_2: false,
      isLoading_3: false,
      isLoading_4: false,
      isLoading_5: false,
      isLoading_6: false,
      currentTab: 0,
      progress: 1,
      progressData: [],
      loanData: [],
      changeData: [],
      premiumData: [],
      claimData: [],
      MarketingData:[],
      marketingDataOpen: false,
      codedescData:{},
    }
  },
  components: { 
    Avatar, CustomerInfo, TabHeader,MarketingDataModal // basic component  
  },
  computed: {
    ...mapGetters(['BaseUrl'])
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    openMaketingDataModal(MarketingData){
      this.codedescData = MarketingData
      this.panelOpen('marketingDataOpen')
    },
    initCustomerDetail6() {
      this.isLoading_1 = true
      queryProgressNewConTract(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body && result.body.data_list) {
          this.progressData = result.body.data_list.map(item => {
            return {
              no: item.APPLY_NO,
              target: item.INSD_NAME,
              client: item.APC_NAME,
              insuranceNo: item.POLICY_NO,
              type: item.PROD_NM,
              date: item.INPUT_DATE,
              status: item.APLY_STS_ALL
            }
          })
        }
        this.isLoading_1 = false
      })

      this.isLoading_2 = true
      queryInsuranceClaimRecord(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body && result.body.AA_LIST) {
          this.claimData = result.body.AA_LIST.map(item =>{
            return {
              APLY_NO: item.APLY_NO,
              OCR_ID: item.OCR_ID,
              OCR_NAME: item.OCR_NAME,
              APLY_DIV_NAME: item.APLY_DIV_NAME,
              APLY_NAME: item.APLY_NAME,
              APLY_DATE: item.APLY_DATE,
              DECD_EMP_NAME: item.DECD_EMP_NAME,
              DECD_EXT: item.DECD_EXT,
              APLY_STS_NAME: item.APLY_STS_NAME
            }
          })
        }
        this.isLoading_2 = false
      })

      this.isLoading_3 = true
      queryPayRecord(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body && result.body.CHANGE_LIST){
          this.changeData = result.body.CHANGE_LIST.map(item =>{
            return {
              APLY_NO: item.APLY_NO,
              AGNT_NAME: item.AGNT_NAME,
              INSD_NAME: item.INSD_NAME,
              PROD_NAME: item.PROD_NAME,
              APLY_DATE: item.APLY_DATE,
              POLICY_NO: item.POLICY_NO,
              TRN_KIND: item.TRN_KIND,
              TRN_KIND_CH: item.TRN_KIND_CH,
              APLY_STS_CH: item.APLY_STS_CH,
              EFT_DATE: item.EFT_DATE,
              ACPT_BANK_NO_CH: item.ACPT_BANK_NO_CH,
              ACPT_ACNT_NO: item.ACPT_ACNT_NO,
              APC_NAME: item.APC_NAME
            }
          })
        }
        this.isLoading_3 = false
      })

      this.isLoading_4 = true
      queryIntegratedMarketing(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body){          
           let dataAry = result.body.map(item =>{
            return {
              agidno: item.agidno,
              idno: item.idno,
              name: item.name,
              gpkind: item.gpkind,
              ckind: item.ckind,
              crshp: item.crshp,
              tdte: item.tdte,
              handle: item.handle,
              cdte: item.cdte,
              sdte: item.sdte,
              cecmn: item.cecmn,
              updcode: item.updcode,
              recode: item.recode,
              cecmnym: item.cecmnym,
              cecobcd: item.cecobcd,
              bdte: item.bdte,
              buycmn: item.buycmn,
              buycmnym: item.buycmnym,
              cecobcd_nm: item.cecobcd_nm,
              codedesc: item.codedesc
            }
          })
          dataAry.sort(function(a, b){return (b.tdte > a.tdte)? 1 : -1})
          this.MarketingData = dataAry
          
          if(this.MarketingData.codedesc){
            this.codedescData = this.MarketingData[0].codedesc
          }
        }
        else{
          this.MarketingData = []
        }
        // console.log("MarketingData:"+ this.MarketingData.name);
        
        this.isLoading_4 = false
      })


      this.isLoading_5 = true
      queryProgressLoan(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body && result.body.data_list) {
          this.loanData = result.body.data_list
        } else {
          this.loanData = []
        }
        this.isLoading_5 = false
      })

      this.isLoading_6 = true
      queryPremiumRecord(this.$store.state.userInfo.id, this.$store.state.custInfo.ID).then(result =>{
        if(result.body && result.body.AUTH_LIST){
          this.premiumData = result.body.AUTH_LIST.map(item =>{
            return {
              AUTH_ID: item.AUTH_ID,
              AUTH_NO: item.AUTH_NO,
              BANK_NAME: item.BANK_NAME,
              CURRTYPE: item.CURRTYPE,
              STATUS: item.STATUS,
              CFM_STS: item.CFM_STS,
              RJT_STS: item.RJT_STS,
              MEMO: item.MEMO
            }
          })
        }
        this.isLoading_6 = false
      })
    }
  },
  beforeMount () {
    let routeNumber = (this.$route.name.split(/-/g)).reverse()
    this.currentTab = Number(routeNumber[0])
  },
  async mounted () {
    this.initCustomerDetail6()
  }
}
</script>
